<template>
  <div class="hello">
    <div class="tabs">
      <ul v-bind:class="{ incFont: isActive }">
        <li>
          <a
            href="http://www.aabhaskharel.com/assets/resumeAabhasKharel.pdf"
            target="_blank"
            v-bind:class="{ incFont: isActive }"
          >résumé</a>
        </li>
        <li>
          <a
            href="https://www.linkedin.com/in/aabhaskharel"
            target="_blank"
            v-bind:class="{ incFont: isActive }"
          >linkedin</a>
        </li>
        <li>
          <a
            href="https://github.com/aabhaskharel"
            target="_blank"
            v-bind:class="{ incFont: isActive }"
          >github</a>
        </li>
        <li>
          <a
            href="https://devpost.com/aabhaskharel"
            target="_blank"
            v-bind:class="{ incFont: isActive }"
          >devpost</a>
        </li>
      </ul>
    </div>

    <div class="main">
      <img src="../assets/01.jpeg" />
      <h1>{{ msg }}</h1>
      <br />
      <h2 class="output">{{output}}</h2>
    </div>

    <div class="skillsArea">
      <h3>
        <b>Skills</b>
      </h3>
      <ul>
        <li>
          C/C++
          <v-progress-circular
            :rotate="-90"
            :size="37"
            :width="4"
            :value="value+30"
            color="white"
            class="progress"
          >{{ value+30 }}</v-progress-circular>
        </li>
        <br />
        <li>
          HTML/CSS
          <v-progress-circular
            :rotate="360"
            :size="37"
            :width="4"
            :value="value+35"
            color="#7bcfff"
            class="progress"
          >{{ value+35 }}</v-progress-circular>
        </li>
        <br />
        <li>
          JavaScript
          <v-progress-circular
            :rotate="360"
            :size="37"
            :width="4"
            :value="value+20"
            color="yellow"
            class="progress"
          >{{ value+20 }}</v-progress-circular>
        </li>
        <br />
        <li>
          Vue.js
          <v-progress-circular
            :rotate="360"
            :size="35"
            :width="4"
            :value="value+20"
            color="#41b883"
            class="progress"
          >{{ value+20 }}</v-progress-circular>
        </li>
        <br />
        <li>
          Python
          <v-progress-circular
            :rotate="360"
            :size="37"
            :width="4"
            :value="value+20"
            color="gray"
            class="progress"
          >{{ value+20 }}</v-progress-circular>
        </li>
        <br />
        <!-- <li>
          Flutter
          <v-progress-circular
            :rotate="360" 
            :size="37"
            :width="4"
            :value="value+20"
            color="blue"
            class="progress"
          >{{ value+20 }}</v-progress-circular>
        </li>-->
      </ul>
    </div>

    <div class="projectsArea">
      <!-- <h2>
        <b>What I have done so far</b>
      </h2>-->
      <ul>
        <v-timeline>
          <v-timeline-item v-for="(project, i) in projects" :key="i" :color="project.color" small>
            <template v-slot:opposite>
              <span
                :class="`headline font-weight-bold ${project.color}--text`"
                v-text="project.project"
              ></span>
            </template>
            <div class="py-3" @click="openLink(project.url)">
              <p :class="`headline font-weight-light mb-3 ${project.color}--text`">{{project.title}}</p>
              <!-- <img :src="project.image" /> -->
              <br />
              {{project.info}}
            </div>
          </v-timeline-item>
        </v-timeline>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      interval: {},
      value: 0,

      msg: "Aabhas Kharel",
      resumeLink: "",
      githubLink: "",
      devpostLink: "",

      isActive: false,
      typeFinish: false,
      i: 0,
      speed: 30,
      output: "",
      out:
        "Hey! I am a Sotware Engineering Senior at The University of Texas at Arlington.",

      projects: [
        // {
        //   title: "PutLetter - word game",
        //   image: "/static/putletter.png",
        //   url: "https://github.com/vektor-tech/SmartJournal",
        //   color: "yellow",
        //   project: "March 2019",
        //   info:
        //     "This is a web app developed using Vue.js which allowes two users to play word guessing game."
        // },
        // {
        //   title: "Seeker - an app for visually impaired",
        //   image: "/static/seeker.jpg",
        //   url: "https://github.com/vektor-tech/hacklahoma2019",
        //   color: "pink",
        //   project: "February 2019",
        //   info: "Made during Hacklahoma 2019 and won WOLFRAM's challenge"
        // },
        // {
        //   title: "Smart Journal",
        //   image: "/static/smartjournal.png",
        //   url: "https://github.com/vektor-tech/SmartJournal",
        //   color: "green",
        //   project: "October 2018",
        //   info:
        //     "Made during HackUTA 18 at The University of Texas at Arlington and won the title - Facebook’s Best Community Building Project"
        // },
        // {
        //   title: "Swapit - Airplane seat exchange made easy",
        //   image: "/static/swapit.png",
        //   url: "https://github.com/vektor-tech/SwapIt",
        //   color: "amber",
        //   project: "October 2018",
        //   info: ""
        // },
        // {
        //   title: "Chevron Task Assiging System",
        //   image: "/static/chevron.jpg",
        //   url: "",
        //   color: "cyan",
        //   project: "February 2018",
        //   info: ""
        // },
        // {
        //   title: "Peer-to-peer Distributed Blackjack Game",
        //   image: "/static/blackjack.png",
        //   url: "https://github.com/xroshan/BlackJack-Player",
        //   color: "orange",
        //   project: "January 2018",
        //   info:
        //     "An implementation of blackjack's player interface using OpenSplice for peer-to-peer communications and FLTK for user interface."
        // }
      ],
      images: [{ url: "../assets/blackjack.png" }]
    };
  },
  beforeMount() {
    this.typeWriter();
  },
  beforeDestroy() {
    clearInterval(this.interval);
  },
  mounted() {
    this.interval = setInterval(() => {
      if (this.value === 50) {
        return;
      }
      this.value += 10;
    }, 500);
  },
  methods: {
    typeWriter() {
      this.typeFinish = false;
      if (this.i < this.out.length) {
        this.output += this.out.charAt(this.i);
        this.i++;
        setTimeout(this.typeWriter, this.speed);
      } else {
        this.typeFinish = true;
      }
    },
    outsideClick() {
      this.isActive = !this.isActive;
      if (this.typeFinish) this.output = "";
    },
    openLink(url) {
      window.open(url, "_blank");
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.tabs {
  text-align: center;
  margin-top: -4%;
}
.main {
  text-align: center;
  font-size: 15px;
  margin-top: 3%;
}

h1,
h2,
h3 {
  font-weight: normal;
  transition: 4s;
}

ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 5px 20px;
  transition: 4s;
}
a {
  color: #7bcfff;
  text-decoration: none;
  padding: 10px;
  font-size: 25px;
  transition: 0.5s;
}
h1:hover,
h2:hover,
h3:hover,
li:hover {
  color: #7bcfff;
  transition: 0.5s;
}
a:hover {
  color: #d1eeff;
}
output {
  width: 100px;
}
.incFont {
  font-size: 40px;
  color: #7bcfff;
  transition: 0.5s;
}

.skillsArea {
  padding: 5% 0 0 25%;
  font-size: 25px;
}
.progress {
  font-size: 17px;
  margin-left: 20px;
  margin-top: -5px;
}

.projectsArea {
  padding: 5% 0 0 15%;
  font-size: 20px;
}
.py-3 img {
  border-radius: 5%;
  width: 20px;
  height: 20px;
  transition: 0.5s;
}
.py-3 img:hover {
  width: 200px;
  height: 150px;
  transition: 1s;
}

/* for Responsive */
/* @media only screen and (max-width: 600px) {
  .projectsArea {
    padding: 5% 0 0 5%;
    font-size: 15px;
  }

  .py-3 p {
    font-size: 5px;
  }

  .py-3 img {
    width: 130px;
    height: 200px;
  }
} */
</style>



